import { createClient } from '@/lib/supabase/server'
import Link from 'next/link'

export default async function DashboardPage() {
  const supabase = await createClient()
  
  // Collections laden
  const { data: collections, error } = await supabase
    .from('collections')
    .select(`
      *,
      items:items(count)
    `)
    .order('created_at', { ascending: false })

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-slate-900">Dashboard</h1>
          <p className="text-slate-500 mt-1">√úbersicht deiner Sammlungen</p>
        </div>
        <Link
          href="/collections/new"
          className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-2"
        >
          <span>+</span>
          <span>Neue Sammlung</span>
        </Link>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <StatCard
          title="Sammlungen"
          value={collections?.length ?? 0}
          icon="üì¶"
        />
        <StatCard
          title="Items gesamt"
          value={collections?.reduce((acc, c) => acc + (c.items?.[0]?.count ?? 0), 0) ?? 0}
          icon="üè∑Ô∏è"
        />
        <StatCard
          title="Gesch√§tzter Wert"
          value="‚Äì"
          icon="üí∞"
          subtitle="Noch keine Daten"
        />
      </div>

      {/* Collections Grid */}
      {error ? (
        <div className="bg-red-50 text-red-600 p-4 rounded-lg">
          Fehler beim Laden: {error.message}
        </div>
      ) : !collections?.length ? (
        <EmptyState />
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {collections.map((collection) => (
            <CollectionCard key={collection.id} collection={collection} />
          ))}
        </div>
      )}
    </div>
  )
}

function StatCard({ 
  title, 
  value, 
  icon, 
  subtitle 
}: { 
  title: string
  value: number | string
  icon: string
  subtitle?: string
}) {
  return (
    <div className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-slate-500 text-sm">{title}</p>
          <p className="text-3xl font-bold mt-1">{value}</p>
          {subtitle && <p className="text-slate-400 text-xs mt-1">{subtitle}</p>}
        </div>
        <span className="text-3xl">{icon}</span>
      </div>
    </div>
  )
}

function CollectionCard({ collection }: { collection: any }) {
  const itemCount = collection.items?.[0]?.count ?? 0
  
  return (
    <Link 
      href={`/collections/${collection.id}`}
      className="bg-white rounded-xl p-6 shadow-sm border border-slate-200 hover:shadow-md hover:border-blue-300 transition-all group"
    >
      {/* Cover Image oder Placeholder */}
      <div className="h-32 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg mb-4 flex items-center justify-center">
        <span className="text-4xl">üì¶</span>
      </div>
      
      <h3 className="font-semibold text-lg group-hover:text-blue-600 transition-colors">
        {collection.name}
      </h3>
      
      {collection.description && (
        <p className="text-slate-500 text-sm mt-1 line-clamp-2">
          {collection.description}
        </p>
      )}
      
      <div className="flex items-center gap-4 mt-4 text-sm text-slate-400">
        <span>{itemCount} Items</span>
        {collection.is_public && <span>üåê √ñffentlich</span>}
      </div>
    </Link>
  )
}

function EmptyState() {
  return (
    <div className="text-center py-16 bg-slate-50 rounded-xl border-2 border-dashed border-slate-200">
      <span className="text-6xl">üì¶</span>
      <h3 className="text-xl font-semibold mt-4">Noch keine Sammlungen</h3>
      <p className="text-slate-500 mt-2">Erstelle deine erste Sammlung, um loszulegen!</p>
      <Link
        href="/collections/new"
        className="inline-block mt-6 bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors"
      >
        + Erste Sammlung erstellen
      </Link>
    </div>
  )
}
